"use strict";
//----------------------------------------
//  swiper
//----------------------------------------
export default class swiperGroup {
    constructor() {
        this.swipers = [];
    }

    //----------------------------------------
    //   : WORKS
    //----------------------------------------
    make_works( i_swiper, i_name ) {
        this.swipers[i_name] = new Swiper( i_swiper, {
            initialSlide: 2,
            loop: false,
            allowTouchMove: false,  //  
            //  
            centeredSlides: false, //
            speed: 500,
            //effect: "slide",
            effect: "fade",
            fadeEffect: {
            //    crossFade: true,
            },
            spaceBetween: 0,   //
            slidesPerView: 1,   //
            breakpoints: {      //
                599: { slidesPerView: 1 },
                1024: { slidesPerView: 1 },
            },
            //direction: 'vertical' ,
            updateOnWindowResize: true, //  
            /*
            on: {
                slideChange: function () {
                    var prevSlide = this.previousIndex;
                    var currentSlide = this.activeIndex;
                    // 
                    var prevSlideEl = this.slides[prevSlide];
                    var curSlideEl = this.slides[currentSlide];
                    curSlideEl.style.width = '100%';
                    //curSlideEl.style.transform = 'translate3d(0px, 0px, 0px )';
                    prevSlideEl.style.transform = 'translate3d(0px, 0px, 0px )';
                    //console.log( curSlideEl.style.transform );
                }
            }
            ,*/
            autoplay : false
//            autoplay: {
//                delay: 3000,
//                disableOnInteraction: false,
//            }
        });
    }


    //----------------------------------------
    //   : SKILL
    //----------------------------------------
    make_skill( i_swiper, i_name ) {
        // swiperslider
//        this.swipers[this.swipers.length] = new Swiper( i_swiper, {
            this.swipers[i_name] = new Swiper( i_swiper, {
            loop: false,
            allowTouchMove: false,  //  
        /*    
            navigation: {
                nextEl: ".swiper-button-next",
                prevEl: ".swiper-button-prev"
            },
            //  
            scrollbar: {
                el: ".swiper-scrollbar",
                hide: true,
                draggable: false
            },
        */    
            //  
            pagination: {
                el: ".swiper-pagination",
                type: "bullets",
                clickable: "clickable"
            },

            centeredSlides: true, //
            //speed: 2000,
            //effect: "slide",
            //effect: "fade",
            //effect: "cube",
            //effect: "coverflow",
            //effect: "flip",
            //effect: "cards",
            //effect: "creative",

            spaceBetween: 16,   //16px
            slidesPerView: 1,   //2
            autoplay: false,
            breakpoints: {      //
                // 599px
                599: {
                    slidesPerView: 1, //2
                },
                // 1024px
                1024: {
                    slidesPerView: 1, //3
                },
            }
        //    autoplay: {
        //        delay: 3000,
        //        disableOnInteraction: false,
        //    }
        });
    }


    //----------------------------------------
    //  swiper
    //----------------------------------------
    registSwiper( i_swiper, i_name ) {
        switch( i_name )
        {
        case "skill": this.make_skill( i_swiper, i_name );  break;
        case "works": this.make_works( i_swiper, i_name );  break;
        }
    }


    //----------------------------------------
    //  
    //----------------------------------------
    eventRegistration() {
        //  .swiper : swiper
        let swipergroup = document.querySelectorAll('.swiper');
        //  swiper
        swipergroup.forEach((swiper) => {
            let name = swiper.dataset.name;
            //console.log( "[swiper]" + swiper + " : " + name );
            //  swiper
            if( name )
                this.registSwiper( swiper, name );
        });
    }
}
